var DVUI = DVUI || {};

(function($) {
    // get bootstrap screen environment
    // returns 'xs' and 'lg' only
    $.fn.findBootstrapEnvironment = function() {
        var envs = ['xs', 'sm', 'md', 'lg'];

        $el = $('<div>');
        $el.appendTo($('body'));

        for (var i = envs.length - 1; i >= 0; i--) {
            var env = envs[i];

            $el.addClass('hidden-'+env);
            if ($el.is(':hidden')) {
                $el.remove();

                switch(env)
                {
                    case 'sm':
                    case 'md':
                        env = 'lg';
                    break;
                }

                return env
            }
        };
    };

    $.fn.getCurrentPage = function() {
        var current_page = $('[data-dvui-page]').data('dvui-page');

        if (typeof current_page !== 'undefined') {
            return current_page;
        } else {
            return false;
        }
    };

    $.fn.htmlEntities = function(str) {
        return String(str).replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/"/g, '&quot;');
    };

    $.fn.isElementInViewport = function(el) {
        if (el) {
            var rect = el.getBoundingClientRect();

            return rect.bottom > 0 &&
                   rect.right > 0 &&
                   rect.left < (window.innerWidth || document. documentElement.clientWidth) /*or $(window).width() */ &&
                   rect.top < (window.innerHeight || document. documentElement.clientHeight) /*or $(window).height() */;
        } else {
            return false;
        }
    };

    $.fn.scrollbarWidth = function() {
        var parent, child, width;

        if(width===undefined) {
            parent = $('<div style="width:50px;height:50px;overflow:auto"><div/></div>').appendTo('body');
            child=parent.children();
            width=child.innerWidth()-child.height(99).innerWidth();
            parent.remove();
        }

        return width;
    };

    $.fn.mobileFix = function (options) {
        var $parent = $(this),
        $fixedElements = $(options.fixedElements);

        $(document)
        .on('focus', options.inputElements, function(e) {
            $parent.addClass(options.addClass);
        })
        .on('blur', options.inputElements, function(e) {
            $parent.removeClass(options.addClass);

            // Fix for some scenarios where you need to start scrolling
            setTimeout(function() {
                $(document).scrollTop($(document).scrollTop())
            }, 1);
        });

        return this; // Allowing chaining
    };

    // * iOS zooms on form element focus. This script prevents that behavior.
    // * <meta name="viewport" content="width=device-width,initial-scale=1">
    //      If you dynamically add a maximum-scale where no default exists,
    //      the value persists on the page even after removed from viewport.content.
    //      So if no maximum-scale is set, adds maximum-scale=10 on blur.
    //      If maximum-scale is set, reuses that original value.
    // * <meta name="viewport" content="width=device-width,initial-scale=1.0,maximum-scale=2.0,maximum-scale=1.0">
    //      second maximum-scale declaration will take precedence.
    // * Will respect original maximum-scale, if set.
    // * Works with int or float scale values.
    function cancelZoom()
    {
        var d = document,
            viewport,
            content,
            maxScale = ',maximum-scale=',
            maxScaleRegex = /,*maximum\-scale\=\d*\.*\d*/;

        // this should be a focusable DOM Element
        if(!this.addEventListener || !d.querySelector) {
            return;
        }

        viewport = d.querySelector('meta[name="viewport"]');
        content = viewport.content;

        function changeViewport(event)
        {
            // http://nerd.vasilis.nl/prevent-ios-from-zooming-onfocus/
            viewport.content = content + (event.type == 'blur' ? (content.match(maxScaleRegex, '') ? '' : maxScale + 10) : maxScale + 1);
        }

        // We could use DOMFocusIn here, but it's deprecated.
        this.addEventListener('focus', changeViewport, true);
        this.addEventListener('blur', changeViewport, false);
    }

    // Usage:
    // $('input:text,select,textarea').cancelZoom();
    $.fn.cancelZoom = function()
    {
        return this.each(cancelZoom);
    };

    var dvcTmplCache = {};

    $.dvcHandlebars = function () {
        dvcHandlebarHelpers();

        var setTemplate = function (templates) {

            for(var idx in templates) {
                var name = templates[idx];
                var $tmpl = $$("#" + name);
                if ($tmpl.length > 0) {
                    dvcTmplCache[name] = Handlebars.compile($tmpl.html());
                }
            }
        }

        if (typeof arguments[0] !== 'string') {
            var options = arguments[0];
            if (options.hasOwnProperty('templates')) {
                var names;

                if (typeof options.templates !== 'string') {
                    names = options.templates;
                } else {
                    names = options.partials.split(/\s+/g);
                }

                setTemplate(names);
            }
        }
    };

    $.fn.render = function (templateName, data, append) {
        var isAppendOnly = (typeof append !== 'undefined') && (append === true) ? true : false;

        var getTemplate = function(tmpl) {
            var template = null;

            /*
            if (dvcTmplCache[tmpl] === undefined) {
                template = null;
            } else if (dvcTmplCache.hasOwnProperty(tmpl)) {
                template = dvcTmplCache[tmpl]
            }*/

            if (dvcTmplCache.hasOwnProperty(tmpl)) {
                template = dvcTmplCache[tmpl];
            } else {
                if ($$('#' + tmpl).length > 0) {
                    template = Handlebars.compile($$('#' + tmpl).html());
                    dvcTmplCache[tmpl] = template;
                }
            }

            return template;
        }

        var tmpl = getTemplate(templateName);
        if (tmpl !== null) {
            if (isAppendOnly) {
                this.append(tmpl(data));
            } else {
                this.html(tmpl(data));
            }
        }

        return this;
    };

    $.moneyFormat = function(number, format) {

        var format     = format || {},
            commas     = format.commas || true,
            symbol     = format.symbol || "$",
            isNegative = false;

        if (number < 0) {
            isNegative = true;
            number *= -1;
        }

        number = isNaN(number) ? 0 : number
        number = parseFloat(number, 10).toFixed(2);

        if (commas) {
            var count = 0;
            var numArr = number.toString().split("");

            var len = numArr.length - 6;

            for (var i = len; i > 0; i = i - 3) {
                numArr.splice(i, 0, ",");
            }

            number = numArr.join("");
        }

        if (typeof symbol === 'string') {
            number = symbol + number;
        }

        number = (isNegative) ? '-' + number : number;

        return number;

    };

    $.fn.money = function(number, format) {

        var $this = this;

        if (typeof(number) === 'object') {
            //incase just parameters are entered and not a number
            var format = number;
            number = $this.html();
        }

        $this.html($.moneyFormat(number, format));

        return $this;

    };

    $.fn.numericInput = function( options ) {
        var defaults = {
            allowFloat: true,
            allowNegative: false
        };

        var settings = $.extend( {}, defaults, options );
        var allowFloat = settings.allowFloat;
        var allowNegative = settings.allowNegative;

        this.keypress(function (event) {
            var inputCode = event.which;
            var currentValue = $(this).val();

            // Checks the if the character code is not a digit
            if (inputCode > 0 && (inputCode < 48 || inputCode > 57)) {

                // Conditions for a period (decimal point)
                if (allowFloat == true && inputCode == 46) {

                    //Disallows a period before a negative
                    if (allowNegative == true && getCaret(this) == 0 && currentValue.charAt(0) == '-') {
                        return false;
                    }
                    //Disallows more than one decimal point.
                    if (currentValue.match(/[.]/)) {
                        return false;
                    }
                }

                // Conditions for a decimal point
                else if (allowNegative == true && inputCode == 45) {
                    if(currentValue.charAt(0) == '-') {
                        return false;
                    }

                    if(getCaret(this) != 0) {
                        return false;
                    }
                }

                // Allows backspace
                else if (inputCode == 8) {
                    return true;
                }
                // Disallow non-numeric
                else {
                    return false;
                }
            }

            // Disallows numbers before a negative.
            else if(inputCode > 0 && (inputCode >= 48 && inputCode <= 57)) {
                if (allowNegative == true && currentValue.charAt(0) == '-' && getCaret(this) == 0) {
                    return false;
                }
            }
        });

        return this;
    };


    // Private function for selecting cursor position. Makes IE play nice.
    function getCaret(element)
    {
        if (element.selectionStart)
            return element.selectionStart;

        else if (document.selection) //IE specific
        {
            element.focus();

            var r = document.selection.createRange();
            if (r == null)
                return 0;

            var re = element.createTextRange(),
                rc = re.duplicate();
            re.moveToBookmark(r.getBookmark());
            rc.setEndPoint('EndToStart', re);
            return rc.text.length;
        }

        return 0;
    };

    /**
     * Returns true if the given value is empty.
     *
     * @param {any} `value`
     * @return {Boolean}
     * @api public
     */

    $.isEmpty = function(val) {
        if (val === 0 || val === '0') {
            return false;
        }
        if (!val || (Array.isArray(val) && val.length === 0)) {
            return true;
        }
        if (typeof val === 'object' && !Object.keys(val).length) {
            return true;
        }
        return false;
    };

    /**
     * Fix the Error Placment for Mobile for Group Elements
     *
     */
    $.fixErrorPlacement = function () {

        var getEnv = function() {
            var envs = ["xs", "sm", "md", "lg"];

             var $el = $("<div>");
             $el.appendTo($("body"));

             for (var i = envs.length - 1; i >= 0; i--) {
               var env = envs[i];

               $el.addClass("hidden-" + env);
               if ($el.is(":hidden")) {
                 $el.remove();
                 return env;
               }
             }
             return "";
       }

        /* Error Placement Fix for Mobile */
        if (getEnv() == 'xs') {
            var $mobileRow = $('.mobile-row');
            $mobileRow.closest('.form-row').removeClass('form-row');
            $mobileRow.addClass('form-row');
        }

        /* Error Placement Fix for Ipad */
        if (getEnv() == 'sm') {
            var $tabletRow = $('.tablet-row');
            $tabletRow.closest('.form-row').removeClass('form-row');
            $tabletRow.addClass('form-row');
        }

    };

    /** Capture Autofill Event **/

    $.fn.listenForChange = function(options) {
        settings = $.extend({
            interval: 200 // in microseconds
        }, options);

        var jquery_object = this;
        var current_focus = null;

        jquery_object.filter(":input").add(":input", jquery_object).focus( function() {
            current_focus = this;
        }).blur( function() {
            current_focus = null;
        });

        setInterval(function() {
            // allow
            jquery_object.filter(":input").add(":input", jquery_object).each(function() {
                // set data cache on element to input value if not yet set
                if ($(this).data('change_listener') == undefined) {
                    $(this).data('change_listener', $(this).val());
                    return;
                }
                // return if the value matches the cache
                if ($(this).data('change_listener') == $(this).val()) {
                    return;
                }
                // ignore if element is in focus (since change event will fire on blur)
                if (this == current_focus) {
                    return;
                }
                // if we make it here, manually fire the change event and set the new value
                $(this).trigger('change');
                $(this).data('change_listener', $(this).val());
            });
        }, settings.interval);
        return this;
    };

    window.$$ = $;

})(jQuery);

window.$$ = $.noConflict(true);

DVUI.validateForm = function($form) {
    var invalid = false;
    $form.find('.inputElement').each(function () {
        var parentHidden  = $(this).parent().parent().css('display') == 'none' || $(this).parents('.hidden').length || $(this).parents('.hide').length;
        var isHiddenField = parentHidden || $(this).hasClass(".hide") || ($("#" + $(this).attr('id') + "id-base").css('display') == 'none');
        if (!isHiddenField) {
             $(this).trigger('focusout');
        }
    });
    setTimeout (function() {
        /* after all fields have triggered focus out - lets see if there are still any more errors */
        if ($('div.standardFormElement.error:visible').length > 0 ||
            $('ul.inlineErrors:visible > li:visible').length  > 0) {
            invalid = true;
        }
        if (!invalid){
            $form.submit();
        } else {
            $('html, body').animate({ scrollTop: $("div.hasErrors:first").offset().top}, 1000);
        }
    }, 500 );
};

DVUI.updateAddressFields = function ($form) {
    var updateAddressVisibility = function () {
        var country = $form.find('select#country').val();
        var fieldSelectors = [
            "#territoryid-base",
            "#stateid-base",
            "#provinceid-base",
            "#zipCodeid-base",
            "#postalCodeid-base",
            "#defaultPostalCodeid-base",
            '#stateDisclaimer'
        ];
        var countryMap = {
            "US" : [0, 1, 0, 1, 0, 0, 1],
            "CA" : [0, 0, 1, 0, 1, 0, 0],
            "OTHER" : [1, 0, 0, 0, 0, 1, 0]
        };
        var fieldValues = (countryMap[country]) ? countryMap[country] : countryMap["OTHER"];

        for (var i = 0; i < fieldSelectors.length; i++) {
            if (fieldValues[i]) {
                $(fieldSelectors[i]).show();
            } else {
                var $fieldName = fieldSelectors[i].replace('id-base', '');
                $($fieldName + '-validationError').remove();
                $(fieldSelectors[i]).hide().removeClass('hasErrors');
                $(fieldSelectors[i]).find('label').removeClass('hasErrors');
                $(fieldSelectors[i]).find('.error').removeClass('error');
            }
        }
    };

    $form.delegate('select#country', 'change', function() {
        updateAddressVisibility();
    });
    updateAddressVisibility();
};

DVUI.toggleSubmitBtn = function($form) {
    var $formFields = $form.find('input, select'),
        $btnSubmit = $form.find('a.formSubmit'),
        isValid = false;

    setTimeout(function(){
        $formFields.each(function(k,v) {
            if ($(this).closest('div.jsvfElement').is(':visible') ||
                    $(this).closest('.richCheckBox').is(':visible')) {
                var isRequired = $(this).prop('required');
                if (isRequired) {
                    if ($(this).is('select')) {
                        isValid = $(this).val() != '';
                    } else if ($(this).is(':checkbox')) {
                        isValid = $(this).is(':checked');
                    } else {
                        isValid = ($(this).val() != '') && !$(this).closest('div.formInputElement').hasClass('hasErrors');
                    }
                } else {
                    isValid = !$(this).closest('div.formInputElement').hasClass('hasErrors');
                }
                if (!isValid) {
                    return isValid;
                }
            }
        });
        if (isValid) {
            $btnSubmit.removeClass('disabled');
        } else {
            $btnSubmit.addClass('disabled');
        }
    }, 500);
};

/**
 * Custom Error Handling - Show Hide Inline Error
 */

DVUI.showHideError = function ($el, showError, messageKey)  {
    var messageObj = $el.data('error-message');
    var $inputWrapper = $el.closest('div.formInputElement');
    var id = $el.prop('id') + '-validationError';

    var $groupWrapper = $el.closest('dl').closest('div');
    var isGroup = $groupWrapper.length;

    if (showError) {
        if (messageKey in messageObj) {
            var $errorItem = $$('<li/>', {
                'class': messageKey,
                'id': id,
                'data-id': id,
                'aria-live':"polite"
            });
            $errorItem.text(messageObj[messageKey]);
            $inputWrapper.append($errorItem);
            if (isGroup) {
                var $groupErrorWrapper = $groupWrapper.find('ul.inlineErrors');
                if ($groupErrorWrapper.length) {
                    $groupErrorWrapper.find('li.'+messageKey).remove();
                    $groupErrorWrapper.prepend($errorItem);
                } else {
                    $groupWrapper.append($$('<ul/>', {'class':"inlineErrors"}).append($errorItem));
                }
            } else {
                $inputWrapper.find('.inlineErrors').remove();
                $inputWrapper.append($$('<ul/>', {'class':"inlineErrors"}).append($errorItem));
            }
            $inputWrapper.find('.standardFormElement').addClass('error');
            $inputWrapper.addClass('hasErrors');
            $inputWrapper.find('label').addClass('hasErrors');
        }
    } else  {
        if (isGroup) {
            var $groupErrorWrapper = $groupWrapper.find('ul.inlineErrors');
            if ($groupErrorWrapper.find('li').length > 1) {
                $groupErrorWrapper.find('#'+id).remove();
            } else {
                $groupErrorWrapper.remove();
            }
        } else {
            $inputWrapper.find('ul.inlineErrors').remove();
        }
        $inputWrapper.removeClass('hasErrors');
        $inputWrapper.find('label').removeClass('hasErrors');
        $inputWrapper.find('.standardFormElement').removeClass('error');
    }
};
